import React, {useEffect, useState} from 'react';
import {Typography, Icon} from 'antd';

function Contact() {
  const [spinning, setSpinning] = useState(true);

  useEffect(function () {
    setTimeout(function () {
      setSpinning(false);
    }, 1500);
  }, []);

  return (
    <>
      <Typography.Paragraph copyable={{ text: 'erich.menezes1@gmail.com' }}>email address
      </Typography.Paragraph>
      <a href='https://www.linkedin.com/in/erichmenezes/' target='_blank' rel='noopener noreferrer'>
        <Icon style={{fontSize: '25px'}} type='linkedin' spin={spinning} />
      </a>
    </>
  )
}

export default Contact;
